<template>
  <v-dialog
    v-model="dialog"
    width="500"
    fullscreen
    hide-overlay
    transition="dialog-bottom-transition"
  >
    <template v-slot:activator="{ on, attrs }">
      <v-btn fixed bottom right fab color="primary" v-bind="attrs" v-on="on">
        <v-icon large>mdi-plus-circle</v-icon>
      </v-btn>
    </template>
    <v-card>
      <v-toolbar dark color="primary">
        <v-btn icon dark @click="dialog = false">
          <v-icon>mdi-close</v-icon>
        </v-btn>
        <v-toolbar-title>Stwrz ankiet</v-toolbar-title>
        <v-spacer></v-spacer>
      </v-toolbar>
      <div class="mx-md-16 mt-md-10 mt-3">
        <div
          v-if="!create"
          class="d-flex justify-center align-center"
          style="height: 250px"
        >
          <v-btn v-if="!next" large color="primary" @click="create = true"
            >stwrz ankiet</v-btn
          >
          <v-btn v-else large color="primary" @click="create = true"
            >stwrz kolejn ankiet</v-btn
          >
        </div>
        <div v-else>
          <v-row class="px-5">
            <v-col cols="12" md="6">
              <div class="d-flex justify-end">
                <v-btn color="error" text @click="deleteSurveyClick"
                  >Usu ankiet</v-btn
                >
              </div>
              <v-form
                v-model="valid"
                ref="surveyForm"
                class="pr-2"
                :class="!$vuetify.breakpoint.mobile ? 'borderClass' : ''"
              >
                <v-text-field
                  outlined
                  class="mb-2"
                  v-model="survey.name"
                  :counter="100"
                  :rules="nameRules"
                  label="Nazwa ankiety"
                  required
                ></v-text-field>
                <v-text-field
                  outlined
                  class="mb-2"
                  v-model="survey.treatment"
                  :counter="100"
                  :rules="treatmentRules"
                  label="Nazwa zabiegu"
                  required
                ></v-text-field>
                <v-textarea
                  outlined
                  rows="1"
                  v-model="survey.description"
                  auto-grow
                  clearable
                  label="Opis ankiety (opcjonalny)"
                >
                </v-textarea>
              </v-form>
            </v-col>
            <v-col
              cols="12"
              md="6"
              v-for="(field, i) in survey.fields"
              :key="i"
            >
              <div
                class="d-flex justify-end"
              >
                <v-btn color="error" text @click="survey.fields.splice(i, 1)">Usu pole</v-btn>
              </div>
              <NewField :field="field" :number="i"/>
            </v-col>
          </v-row>
          <div class="d-flex px-5">
            <v-btn color="primary mb-5" @click="addFliedClick"
              >Dodaj pole</v-btn
            >
            <v-spacer></v-spacer>
            <v-btn color="primary mb-5" @click="saveClick" :loading="loader"
              >Utwrz ankiet</v-btn
            >
          </div>
        </div>
      </div>
    </v-card>
  </v-dialog>
</template>

<script>
import NewField from "@/components/NewField";
import { mapGetters, mapActions } from "vuex";
export default {
  components: {
    NewField,
  },
  data: () => ({
    dialog: false,
    next: false,
    create: false,
    loader: false,
    valid: true,
    survey: {
      name: "",
      treatment: "",
      description: "",
      fields: [],
    },
    nameRules: [
      (v) => !!v || "Nazwa ankiety jest wymagana",
      (v) =>
        (v && v.length <= 100) ||
        "Nazwa ankiety musi by krtsza ni 100 znakw",
    ],
    treatmentRules: [
      (v) => !!v || "Nazwa zabiegu, ktrego dotyczy ankieta jest wymagana",
      (v) =>
        (v && v.length <= 100) ||
        "Nazwa zabiegu musi by krtsza ni 100 znakw",
    ],
  }),
  computed: mapGetters(["getSurveyAlert"]),
  methods: {
    ...mapActions(["createSurvey"]),
    addFliedClick() {
      this.survey.fields.push({
        data: null,
        name: "",
        description: "",
        required: true,
        type: "text",
        options: [
          {
            text: "",
          },{
            text: "",
          },{
            text: "",
          },
        ],
      });
    },
    async saveClick() {
      if (!this.$refs.surveyForm.validate()) return;
      this.loader = true;
      this.next = true;
      this.survey.fields.forEach((field) => {
        if (field.type !== "select" && field.type !== "radio")
          delete field.options;
      });
      await this.createSurvey(this.survey);
      this.loader = false;
      this.survey = {
        name: "",
        treatment: "",
        description: "",
        fields: [],
      };
      this.create = false;
    },
    deleteSurveyClick() {
      this.survey = {
        name: "",
        treatment: "",
        description: "",
        fields: [],
      };
      this.create = false;
    },
  },
};
</script>