<template>
  <div>
    <v-form>
      <v-text-field
        outlined
        class="mb-2"
        v-model="field.name"
        :counter="250"
        :rules="nameRules"
        :label="question"
        required
      ></v-text-field>
      <v-textarea
        outlined
        rows="1"
        v-model="field.description"
        auto-grow
        clearable
        label="Opis (opcjonalny)"
      >
      </v-textarea>
      <v-select
        outlined
        label="Typ pola"
        :rules="[(v) => !!v || `Typ pola jest wymagany`]"
        :items="types"
        v-model="type"
        item-text="name"
        item-value="val"
      >
      </v-select>
      <div :hidden="type > 4 || type < 3">
        <v-text-field
          v-for="(option, i) in field.options"
          :key="i"
          v-model="option.text"
          outlined
          :rules="[(v) => !!v || 'Opcja jest wymagana']"
          label="Opcja"
          append-outer-icon="mdi-minus"
          @click:append-outer="field.options.splice(i, 1)"
          required
        ></v-text-field>
        <v-btn text color="primary" class="mt-n1 mb-1" @click="field.options.push({text: ''})">dodaj opcje</v-btn>
      </div>
    </v-form>
    <v-divider class="mb-3 primary"></v-divider>
  </div>
</template>

<script>
export default {
  props: ['field', 'number'],
  data: () => ({
    name: "",
    nameRules: [
      (v) => !!v || "Nazwa pola jest wymagana",
      (v) =>
        (v && v.length <= 250) || "Nazwa pola musi by krtsza ni 250 znakw",
    ],
    description: "",
    type: 1,
    types: [
      {
        name: "Tekst",
        text: 'text',
        val: 1,
      },
      {
        name: "Liczba",
        text: 'number',
        val: 2,
      },
      {
        name: "Jednokrotny wybr",
        text: 'select',
        val: 3,
      },
      {
        name: "Wielokrotny wybr",
        text: 'radio',
        val: 4,
      },
      {
        name: "Tak/Nie",
        text: 'truefalse',
        val: 5,
      },
    ],
  }),
  watch: {
    type(val) {
      this.field.type = this.types[val - 1].text
    },
  },
  computed: {
    question() {
      return `Pytanie ${this.number + 1}`
    }
  }
};
</script>